#page-preloader {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: #fff;
  z-index: 100500;
}

#page-preloader .spinner {
  width: 64px;
  height: 64px;
  position: absolute;
  left: 50%;
  top: 50%;
  background: url('/images/preloader.gif') no-repeat 50% 50%;
  margin: -32px 0 0 -32px;
}
.cart-order{
  margin:0 auto;
}
.cart-order input{
  text-align:right;
}

.book-list .thumbnail .caption{
  position:relative;
  z-index:40;
}

.book-list figure {
  position: relative;
}
.book-list .thumbnail img{
  width:100%;
}

.dropdown-menu td{
  font-size: 12px;
}
.rating{
  display: inline-block;
  margin: 0;
  padding: 0;
}
.rating li{
  color: #ddd;
  display: inline-block;
  list-style-type: none;
  padding: 1px;
  text-shadow: .05em .05em #aaa;
}
.rating li.filled{
  color: #d17581;
}
.itemButton{
  margin-top: 20px;
  text-align: center;
}
.blosd button{
  cursor: default;
}
.rating li i{
  font-family: 'Glyphicons Halflings';
}
.rating li i:before{
  content: "\e006";
}
.thumbnail{
  overflow: hidden;
}
.header{
  color: #36A0FF;
  font-size: 27px;
  padding: 10px;
}
.reviews-forms{
  display: none;
}
.delete-cart{
  background: url(/images/trash_16x16.gif) no-repeat 0 0;
  display: block;
  height: 16px;
  width: 16px;
}
.delete-cart:hover{
  cursor: pointer;
}
.books .book-list{
  display: inline-block;
  float: none;
  vertical-align: top;
}
.well{
  border-radius: 0 0 4px 4px;
}
.open>.dropdown-menu{
  min-width: 100%;
  z-index: 1001;
}
.open>.dropdown-menu > div,.open>.dropdown-menu{
  background-color: #f9f9f9;
  margin: 1px;
}
.open>.dropdown-menu .emty-cart{
  text-align: center;
}
.open>.dropdown-menu .item-row div{
  text-align: center;
}
.table-list{
  text-align: center;
  width: 100%;
}
.table-list td span{
  display: block;
  margin: 0 auto;
  text-align: center;
}
.table-list tr{
  background-color: #f9f9f9;
  border-bottom:1px solid gray;
}
.table-list tr td{
  padding: 8px;
}
.item-checout .first-column-checkout{
  width: 12%;
}
.item-checout td{
  border-bottom: 1px solid #ddd;
  border-top: none !important;
  text-align: center;
  vertical-align: middle !important;
}
.table-bordered{
  border-collapse: separate;
  border-radius: 4px;
}
.total-user-checkout{
  margin-right: 20px;
  margin-top: 15px;
}
.btn-user-checkout .btn{
  margin-bottom: 10px;
  width: 100%;
}
.item-checout .delete-cart{
  display: block;
  margin: 0 auto;
  text-align: center;
}
.total{
  margin: 5px 5px 5px 0;
}
.check-b{
  margin: 0 5px 5px 0;
}
.x-change{
  margin-right: 5px;
}
.modal-cart-total{
  line-height: 34px;
  margin-right: 15px;
}
.bt-shop{
  text-align: center;
}
.top-m{
  margin-top: 15px;
}
.dropdown .btn-inverse{
  background:#50a8e1;
  border:1px solid #2d6898;
}
.dropdown .btn-inverse:hover{
  background:rgba(80,168,225,.9);
}
.btn-inverse{
  background-color: #363636;
  background-image: linear-gradient(to bottom,#444,#222);
  background-image: -moz-linear-gradient(top,#444,#222);
  background-image: -o-linear-gradient(top,#444,#222);
  background-image: -webkit-gradient(linear,0 0,0 100%,from(#444),to(#222));
  background-image: -webkit-linear-gradient(top,#444,#222);
  background-repeat: repeat-x;
  border-radius: 0;
  color: #fff;
  text-shadow: 0 -1px 0 rgba(0,0,0,0.25);
}
nav.navbar-inverse{
  -webkit-box-shadow: 0px -3px 0px rgba(50, 50, 50, 0.2) inset;
  box-shadow: 0px -3px 0px rgba(50, 50, 50, 0.2) inset;
  background-color: #50a8e1;
  border-color: #50a8e1;
}
nav.navbar-inverse a.navbar-brand,nav.navbar-inverse .navbar-nav>li>a{
  color:#fff;
}
nav.navbar-inverse .navbar-nav>li>a:hover,nav.navbar-inverse .navbar-nav>li>a.Active{
  background:#fff;
  -webkit-box-shadow: 0px -3px 0px rgba(50, 50, 50, 0.2) inset;
  box-shadow: 0px -3px 0px rgba(50, 50, 50, 0.2) inset;
  color: #50a8e1;
}

.btn-inverse:hover{
  background: #505050;
}
#dLabel{
  color: #FFF;
  font-family: 'Open Sans', sans-serif;
  font-size: 12px;
  line-height: 18px;
}
.open #dLabel:hover{
  color: #444;
}
.open #dLabel{
  background-color: #FFFFFF;
  background-image: none;
  box-shadow: none;
  color: #666;
  text-shadow: none;
}

.form-group .help-block {
  display: none;
}

.form-group.has-error .help-block {
  display: block;
}
.column{
  border-bottom:1px dashed #E5E5E5;
  clear:both;
  overflow: hidden;
}
.t-details{
  font-size: 1em;
  line-height: 20px;
  width: 100%;
  color: #CCC;
  border-bottom:1px dashed #E5E5E5;
  clear:both;
  overflow: hidden;
}
.column .ab-details-title{
  float:left;
  font-size: 1.5em;
  line-height: 50px;
  color: #666;
}
.ab-details-info{
  float: left;
  line-height: 55px;
  margin-left: 10px;
  font-size:12px;
  color: #777;
  text-shadow: 1px 1px 1px rgba(255, 255, 255, 1);
}

.slide-image {
  width: 100%;
}

.carousel-holder {
  margin-bottom: 30px;
}

.carousel-control,
.item {
  border-radius: 4px;
}

.thumbnail img {
  display:inline-block !important;
}

.thumbnail {
  padding: 0;
}

